import { LOGIN, SIGNUP, LOGOUT, LOGIN_NULL, LOGIN_MANUFACTURE } from "../types";


export const signup = (payload) => {
    return {
        type: SIGNUP,
        payload
    };
}

export const signin = (payload) => {
    return {
        type: LOGIN,
        payload
    };
}

export const logout = (payload) => {
    return {
        type: LOGOUT,
        payload
    };
}

export const login_null = () => {
    return {
        type: LOGIN_NULL
    };
}

export const signinManufacture = (payload) => {
    return {
        type: LOGIN_MANUFACTURE,
        payload
    };
}
