import { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useTheme } from '@mui/material/styles';
import { Box, Button, FormControl, FormHelperText, Grid, IconButton, InputAdornment, InputLabel, OutlinedInput } from '@mui/material';
import { login_null, signinManufacture } from '../../../store/actions/index';
import * as Yup from 'yup';
import { Formik } from 'formik';
import useScriptRef from 'hooks/useScriptRef';
import AnimateButton from 'ui-component/extended/AnimateButton';
import Visibility from '@mui/icons-material/Visibility';
import VisibilityOff from '@mui/icons-material/VisibilityOff';
import constants from '../../../utilities/constants';
import { Navigate } from 'react-router-dom';
import { useNavigate } from 'react-router-dom';

import CryptoJS from 'crypto-js';
let keySize = 256;
let iterations = 100;

const FirebaseLogin = ({ ...others }) => {
    const theme = useTheme();
    const navigate = useNavigate();
    const constant = constants.getConstant();
    const dispatch = useDispatch();
    const scriptedRef = useScriptRef();
    const [showPassword, setShowPassword] = useState(false);
    const [buttonText, setButtonText] = useState('Sign In');

    useEffect(() => {
        const isAuthenticated = Boolean(sessionStorage.getItem('isAuthenticated'));
        const Menu = sessionStorage.getItem('Menu');
        const Session = sessionStorage.getItem('Session');
        const UserID = sessionStorage.getItem('UserID');

        if (isAuthenticated === true && Menu.length > 0 && Session !== '' && UserID !== '') {
            return <Navigate to={'/App'} replace />;
        }
    }, []);

    const loginRes = useSelector(({ loginRes }) => loginRes);

    // setting login response
    useEffect(() => {
        console.log({ loginRes });
        if (!loginRes.isAuthenticatedManufacture && loginRes.error) {
            console.log(loginRes.error);
        }
        if (loginRes.isAuthenticatedManufacture) {
            console.log({ har: loginRes.isAuthenticatedManufacture })
            sessionStorage.setItem('isAuthenticated', JSON.stringify(loginRes.isAuthenticatedManufacture));
            sessionStorage.setItem('webtoken', JSON.stringify(loginRes.userManufacture.webtoken));
            sessionStorage.setItem('UserID', JSON.stringify(loginRes.userManufacture.UserData.UserID));
            sessionStorage.setItem('UserName', JSON.stringify(loginRes.userManufacture.UserData.UserName));
            sessionStorage.setItem('Designation', JSON.stringify(loginRes.userManufacture.UserData.Designation));
            sessionStorage.setItem('ContactNo', JSON.stringify(loginRes.userManufacture.UserData.ContactNo));
            sessionStorage.setItem('Email', JSON.stringify(loginRes.userManufacture.UserData.Email));
            sessionStorage.setItem('CompID', JSON.stringify(loginRes.userManufacture.UserData.CompID));
            sessionStorage.setItem('CompCode', JSON.stringify(loginRes.userManufacture.UserData.CompCode));
            sessionStorage.setItem('CompName', JSON.stringify(loginRes.userManufacture.UserData.CompName));
            sessionStorage.setItem('Menu', JSON.stringify(loginRes.userManufacture.Menu));
            sessionStorage.setItem('Roles', JSON.stringify(loginRes.userManufacture.Roles));
            //  navigate('/Dashboard', { replace: true });
            window.location = '/Manufacturing/Dashboard'
            dispatch(login_null());
        }
    }, [loginRes, dispatch]);

    const handleClickShowPassword = () => {
        setShowPassword(!showPassword);
    };

    const handleMouseDownPassword = (event) => {
        event.preventDefault();
    };

    const encrypt = (msg, pass) => {
        let salt = CryptoJS.lib.WordArray.random(128 / 8);

        let key = CryptoJS.PBKDF2(pass, salt, {
            keySize: keySize / 32,
            iterations: iterations
        });

        let iv = CryptoJS.lib.WordArray.random(128 / 8);

        let encrypted = CryptoJS.AES.encrypt(msg, key, {
            iv: iv,
            padding: CryptoJS.pad.Pkcs7,
            mode: CryptoJS.mode.CBC
        });
        // var transitmessage =

        return salt.toString() + iv.toString() + encrypted.toString();
    };

    return (
        <>
            <Grid container direction="column" justifyContent="center" spacing={2}>
                <Grid item xs={12}>
                    <Box
                        sx={{
                            alignItems: 'center',
                            display: 'flex'
                        }}
                    >
                        {/* <Divider sx={{ flexGrow: 1 }} orientation="horizontal" /> */}
                    </Box>
                </Grid>
            </Grid>

            <Formik
                initialValues={{
                    email: '',
                    password: '',
                    submit: null
                }}
                validationSchema={Yup.object().shape({
                    email: Yup.string().max(255).required('Username is required'),
                    password: Yup.string().max(255).required('Password is required')
                })}
                onSubmit={async (values, { setErrors, setStatus, setSubmitting }) => {
                    try {
                        console.log({ values });
                        let encryptedPass = encrypt(values.password, constant.SecretKey);
                        let sixdigitsrandom = Math.floor(100000 + Math.random() * 900000);
                        let encryptedsixdigitsrandom = encrypt(sixdigitsrandom.toString(), constant.SecretKey);
                        sessionStorage.setItem('Session', JSON.stringify(encryptedsixdigitsrandom));

                        let payload = {
                            FormName: 'Login',
                            UserName: values.email,
                            Password: encryptedPass,
                            Session: encryptedsixdigitsrandom
                        };

                        dispatch(signinManufacture(payload));
                        setButtonText('Please Wait')
                        if (scriptedRef.current) {
                            setStatus({ success: true });
                            setSubmitting(false);
                        }
                    } catch (err) {
                        console.error(err);
                        if (scriptedRef.current) {
                            setStatus({ success: false });
                            setErrors({ submit: err.message });
                            setSubmitting(false);
                        }
                    }
                }}
            >
                {({ errors, handleBlur, handleChange, handleSubmit, isSubmitting, touched, values }) => (
                    <form noValidate onSubmit={handleSubmit} {...others}>
                        <FormControl fullWidth error={Boolean(touched.email && errors.email)} sx={{ ...theme.typography.customInput }}>
                            <InputLabel htmlFor="outlined-adornment-email-login">Username</InputLabel>
                            <OutlinedInput
                                id="outlined-adornment-email-login"
                                type="email"
                                value={values.email}
                                name="email"
                                onBlur={handleBlur}
                                onChange={handleChange}
                                label="Username"
                                inputProps={{}}
                            />
                            {touched.email && errors.email && (
                                <FormHelperText error id="standard-weight-helper-text-email-login">
                                    {errors.email}
                                </FormHelperText>
                            )}
                        </FormControl>

                        <FormControl
                            fullWidth
                            error={Boolean(touched.password && errors.password)}
                            sx={{ ...theme.typography.customInput }}
                        >
                            <InputLabel htmlFor="outlined-adornment-password-login">Password</InputLabel>
                            <OutlinedInput
                                id="outlined-adornment-password-login"
                                type={showPassword ? 'text' : 'password'}
                                value={values.password}
                                name="password"
                                onBlur={handleBlur}
                                onChange={handleChange}
                                endAdornment={
                                    <InputAdornment position="end">
                                        <IconButton
                                            aria-label="toggle password visibility"
                                            onClick={handleClickShowPassword}
                                            onMouseDown={handleMouseDownPassword}
                                            edge="end"
                                            size="large"
                                        >
                                            {showPassword ? <Visibility /> : <VisibilityOff />}
                                        </IconButton>
                                    </InputAdornment>
                                }
                                label="Password"
                                inputProps={{}}
                            />
                            {touched.password && errors.password && (
                                <FormHelperText error id="standard-weight-helper-text-password-login">
                                    {errors.password}
                                </FormHelperText>
                            )}
                        </FormControl>
                        {errors.submit && (
                            <Box sx={{ mt: 3 }}>
                                <FormHelperText error>{errors.submit}</FormHelperText>
                            </Box>
                        )}

                        <Box sx={{ mt: 2 }}>
                            <AnimateButton>
                                <Button
                                    disableElevation
                                    disabled={isSubmitting}
                                    fullWidth
                                    size="large"
                                    type="submit"
                                    variant="contained"
                                    color="secondary"
                                >
                                    {buttonText}
                                </Button>
                            </AnimateButton>
                        </Box>
                    </form>
                )}
            </Formik>
        </>
    );
};

export default FirebaseLogin;
